import React from 'react'
import {
   Group,
   Header,
   Panel,
   PanelHeaderBack,
   PanelProps,
   Separator,
   SimpleCell,
   Switch,
   Title,
} from '@vkontakte/vkui'
import { useRouter } from '@happysanta/router'
import { PanelHeaderSeparator } from 'components/ui/PanelHeaderSeparator/PanelHeaderSeparator'
import { GroupCard } from 'components/ui/GroupCard/GroupCard'
import { BUTTON_ACTIVE_EFFECT_DELAY } from 'constants/constants'

export const PanelSettingsTableDisplay: React.FC<PanelProps> = (panelProps) => {
   const router = useRouter()
   const onClickBack = () => {
      router.popPage()
   }

   return (
      <Panel {...panelProps}>
         <PanelHeaderSeparator before={<PanelHeaderBack onClick={onClickBack} label={''} />}>
            
         </PanelHeaderSeparator>

         <Group separator='hide'>
            <GroupCard>
               <SimpleCell disabled after={<Switch />}>
                  
               </SimpleCell>
            </GroupCard>

            <GroupCard header={<Header mode='secondary'>Header</Header>}>
               <SimpleCell
                  activeEffectDelay={BUTTON_ACTIVE_EFFECT_DELAY}
                  expandable
                  after={
                     <Title level='3' weight='2'>
                        
                     </Title>
                  }
               >
                   
               </SimpleCell>
               <Separator wide />
               <SimpleCell disabled after={<Switch />}>
                   
               </SimpleCell>
            </GroupCard>

            <GroupCard
               header={
                  <Header mode='secondary' aside='...'>
                     
                  </Header>
               }
            >
               <SimpleCell disabled after={<Switch />}>
                   
               </SimpleCell>
               <Separator wide />
               <SimpleCell disabled after={<Switch />}>
                   
               </SimpleCell>
            </GroupCard>
         </Group>
      </Panel>
   )
}
